.header {
  // height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__photo {
    position: absolute;
    left: 0;
    min-height: 268px;

    @include onTablet {
      position: static;
      margin-bottom: 50px;
      // max-width: 712px;
      min-height: 500px;
    }

    @include onDesktop {
      position: static;
      // width: 580px;
      height: 450px;
      margin-bottom: 0;
      order: 1;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: content;
    object-position: center;
  }

  &__title {
    margin: 90% 0 12px;
    font-weight: bold;
    font-size: 24px;
    line-height: 150%;
    letter-spacing: 5%;
    color: $mainColor;

    text-align: center;

    @include onTablet {
      margin: 0 0 12px;
      padding: 0 120px;
      font-size: 36px;
      line-height: 150%;
      letter-spacing: 5%;
      text-transform: capitalize;
    }

    @include onDesktop {
      margin: 0 0 30px;
      padding: 0;
      font-size: 60px;
      line-height: 130%;
      letter-spacing: 0;
      text-transform: none;

      text-align: start;
    }
  }

  &__slogan {
    margin: 0 0 30px;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: $mainColor;

    text-align: center;

    @include onTablet {
      margin-bottom: 50px;
    }

    @include onDesktop {
      text-align: start;
    }
  }
}
